.terminal {
    /* height: 500px; */
    overflow-y: auto;
    background-color: #3C3C3C;
    color: #C4C4C4;
    padding: 35px 45px;
    font-size: 30px;
    line-height: 1.42;
    font-family: 'IBM Plex Mono', Consolas, Menlo, Monaco, 'Courier New', Courier,
        monospace;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.terminal__line {
    line-height: 2;
    white-space: pre-wrap;
}

.terminal__prompt {
    /* display: flex; */
    align-items: center;
}

.terminal__prompt__label {
    flex: 0 0 auto;
    color: #2d88ff;
    font-weight: bold;
}

.terminal__prompt__label1 {
    flex: 0 0 auto;
    color: #fbff00;
    font-weight: bold;
}

.terminal__prompt__label2 {
    flex: 0 0 auto;
    color: #ff63f2;
    font-weight: bold;
}

.terminal__prompt__label3 {
    flex: 0 0 auto;
    color: #ff0000;
    font-weight: bold;
}

.terminal__prompt__input {
    flex: 1;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    color: #39ff14;
    font-weight: bold;
}

.terminal__prompt__input1 {
    flex: 1;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    color: #ff63f2;
    font-weight: bold;
}

.terminal__prompt__input2 {
    flex: 1;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    color: #fbff00;
    font-weight: bold;
}

.terminal__prompt__input3 {
    flex: 1;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    color: #00FCFF;
    font-weight: bold;
}

